.introduction {
  position: relative; }
  .introduction__intro {
    margin: 20px 0; }
  .introduction__button {
    margin: 20px auto;
    padding: 30px 60px;
    box-shadow: 0 10px 70px -10px rgba(0, 0, 0, 0.6); }
  .introduction__name {
    font-size: 32px; }
  .introduction__usps {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; }
  .introduction__usp {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-basis: 33%;
    min-width: 200px;
    margin: 20px 0;
    padding: 0 5px; }
  .introduction__usp-title {
    margin: 10px 0;
    font-size: 32px;
    text-align: center; }
  .introduction__usp-text {
    text-align: center; }
  .introduction__stages {
    display: flex; }
  .introduction__stage {
    margin: 10px 0;
    display: flex; }
  .introduction__title {
    min-height: 68px;
    width: 100%;
    font-size: 32px;
    flex-direction: row-reverse;
    display: flex;
    align-items: center;
    margin: 25px 0 10px 0; }
    .introduction__title h2 {
      padding: 20px; }
  .introduction__copy {
    margin-top: 60px; }
  .introduction__stage-header {
    font-size: 22px;
    padding: 10px 20px;
    color: #ffffff;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-height: 67px;
    flex-shrink: 0; }
    .introduction__stage-header--reporting {
      flex-basis: 160px;
      max-width: 50%;
      background: #ff7580; }
    .introduction__stage-header--analyzing {
      flex-basis: 190px;
      max-width: 50%;
      background: #ffc37e; }
    .introduction__stage-header--optimizing {
      flex-basis: 220px;
      max-width: 50%;
      background: #54ce98; }
    .introduction__stage-header--empowering {
      flex-basis: 250px;
      max-width: 50%;
      background: #6cb5ed; }
    .introduction__stage-header--innovating {
      flex-basis: 280px;
      max-width: 50%;
      background: #9d92ef; }
  .introduction__stage-content {
    border: 2px solid transparent;
    padding: 10px 20px;
    flex-grow: 1;
    display: flex;
    align-items: center; }
    .introduction__stage-content--reporting {
      border-color: #ff7580; }
    .introduction__stage-content--analyzing {
      border-color: #ffc37e; }
    .introduction__stage-content--optimizing {
      border-color: #54ce98; }
    .introduction__stage-content--empowering {
      border-color: #6cb5ed; }
    .introduction__stage-content--innovating {
      border-color: #9d92ef; }
  .introduction__dimensions {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch; }
  .introduction__dimension {
    flex-basis: 50%;
    margin-bottom: 10px;
    text-align: center;
    display: flex;
    flex-direction: column; }
  .introduction__dimension-header {
    color: #ffffff;
    font-size: 22px;
    padding: 10px; }
    .introduction__dimension-header--data {
      background: #ff7580; }
    .introduction__dimension-header--metrics {
      background: #ff7580; }
    .introduction__dimension-header--skills {
      background: #ffc37e; }
    .introduction__dimension-header--technology {
      background: #ffc37e; }
    .introduction__dimension-header--leadership {
      background: #54ce98; }
    .introduction__dimension-header--culture {
      background: #54ce98; }
    .introduction__dimension-header--strategy {
      background: #6cb5ed; }
    .introduction__dimension-header--agility {
      background: #6cb5ed; }
    .introduction__dimension-header--integration {
      background: #9d92ef; }
    .introduction__dimension-header--empowerment {
      background: #9d92ef; }
  .introduction__dimension-content {
    padding: 10px;
    flex-grow: 1; }
    .introduction__dimension-content--data {
      border-left: 2px solid #ff7580;
      border-right: 1px solid #ff7580;
      border-bottom: 2px solid #ff7580; }
    .introduction__dimension-content--metrics {
      border-left: 1px solid #ff7580;
      border-right: 2px solid #ff7580;
      border-bottom: 2px solid #ff7580; }
    .introduction__dimension-content--skills {
      border-left: 2px solid #ffc37e;
      border-right: 1px solid #ffc37e;
      border-bottom: 2px solid #ffc37e; }
    .introduction__dimension-content--technology {
      border-left: 1px solid #ffc37e;
      border-right: 2px solid #ffc37e;
      border-bottom: 2px solid #ffc37e; }
    .introduction__dimension-content--leadership {
      border-left: 2px solid #54ce98;
      border-right: 1px solid #54ce98;
      border-bottom: 2px solid #54ce98; }
    .introduction__dimension-content--culture {
      border-left: 1px solid #54ce98;
      border-right: 2px solid #54ce98;
      border-bottom: 2px solid #54ce98; }
    .introduction__dimension-content--strategy {
      border-left: 2px solid #6cb5ed;
      border-right: 1px solid #6cb5ed;
      border-bottom: 2px solid #6cb5ed; }
    .introduction__dimension-content--agility {
      border-left: 1px solid #6cb5ed;
      border-right: 2px solid #6cb5ed;
      border-bottom: 2px solid #6cb5ed; }
    .introduction__dimension-content--integration {
      border-left: 2px solid #9d92ef;
      border-right: 1px solid #9d92ef;
      border-bottom: 2px solid #9d92ef; }
    .introduction__dimension-content--empowerment {
      border-left: 1px solid #9d92ef;
      border-right: 2px solid #9d92ef;
      border-bottom: 2px solid #9d92ef; }
