.header {
  display: flex;
  justify-content: center;
  background: #2390e4;
  color: #ffffff;
  padding: 20px; }
  .header__title {
    margin: 10px 0;
    cursor: pointer;
    font-size: 28px;
    line-height: 28px; }

@media only screen and (max-width: 600px) {
  .header {
    padding: 10px; }
    .header__title {
      font-size: 24px;
      line-height: 24px; } }
